* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  font-family: 'Poppins', sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
}

h1{
  text-align: center;
  font-size: 48px;
  line-height: 72px;
  margin: 50px 0;
}

.itemsWrapper{
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(8, 80px);
  gap: 15px;
}


